
<template>
  <div :id="id" :class="className" :style="{ height:height,width:width }" />
</template>

<script>
import echarts from "echarts";
import "echarts-wordcloud/dist/echarts-wordcloud";
import "echarts-wordcloud/dist/echarts-wordcloud.min";


export default {
  props: {
    className: {
      type: String,
      default: "chart"
    },
    id: {
      type: String,
      default: "chart"
    },
    width: {
      type: String,
      default: "100%"
    },
    height: {
      type: String,
      default: "400px"
    },
    data: {
      type: Array,
      default:function(){
        return []
      }
    },
    title: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      chart: null
    };
  },
  mounted() {
    this.initChart();
  },
  watch:{
    data:function(newData){
      console.log(newData)
      this.initChart();
    }
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    initChart() {
      this.chart = echarts.init(document.getElementById(this.id));
      let callback = function(params){
          // const searchWords = params.name;
          // this.$router.push(
          //       `algorithm/search=${searchWords}`,
          //   );
            this.$emit('clickCloud',params)
      }
      callback = callback.bind(this)
      const option = {
        title: {
          text: this.title,
          x: "center"
        },
        backgroundColor: "#fff",

        tooltip: {
          pointFormat: "{series.name}: <b>{point.percentage:.1f}%</b>"
        },
        
         visualMap: {
            show: false,
            min: 0,
            max: 100,
            inRange: {
                colorLightness: [0.9, 0.4]
            }
        },

        series: [
          {
            type: "wordCloud",
            //用来调整词之间的距离
            gridSize: 10,
            //用来调整字的大小范围
            // Text size range which the value in data will be mapped to.
            // Default to have minimum 12px and maximum 60px size.
            sizeRange: [14, 60],
            // Text rotation range and step in degree. Text will be rotated randomly in range [-90,                                                                             90] by rotationStep 45
            //用来调整词的旋转方向，，[0,0]--代表着没有角度，也就是词为水平方向，需要设置角度参考注释内容
            // rotationRange: [-45, 0, 45, 90],
            // rotationRange: [ 0,90],
            rotationRange: [0, 90],
            //随机生成字体颜色
            // maskImage: maskImage,
            textStyle: {
              normal: {
                // color: function() {
                //   return (
                //     "rgb(" +
                //     Math.round(Math.random() * 255) +
                //     ", " +
                //     Math.round(Math.random() * 255) +
                //     ", " +
                //     Math.round(Math.random() * 255) +
                //     ")"
                //   );
                // }
                    color: '#3176C2',
                    shadowBlur: 200,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            },
            //位置相关设置
            // Folllowing left/top/width/height/right/bottom are used for positioning the word cloud
            // Default to be put in the center and has 75% x 80% size.
            left: "center",
            top: "center",
            right: null,
            bottom: null,
            width: "200%",
            height: "200%",
            //数据
            data: this.data
          }
        ]
      };
      this.chart.setOption(option);
      // this.chart.on('click',function(param){
      //     console.log(param);
      //     // this.$emit('clickCloud',param)
      //     var currentUrl = 'http://localhost:8081/#/algorithm/search='+param.name;
      //     window.location.href = currentUrl;  

      //     // const searchWords = param.name;
      //     //   this.$router.push(
      //     //       `homepage/search=${searchWords}`,
      //     //   );
      // })
      this.chart.on('click',callback);
    },
    
  }
};
</script>
<style scoped>
.chartsClass {
  padding-left: 1.2rem;
}
</style>


